@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

h2,
p {
    margin: 0;
}

p {
    font-size: .9rem;
}

body {
    font-family: 'Inter', sans-serif;
}

header {
    background-color: #F55A5A;
    text-align: center;
    color: white;
    padding: 1em 0;
}

.card {
    display: flex;
    padding: 1em;
    justify-content: space-evenly;
    gap: .2em;
    max-width: 800px;
    margin: 0 auto;
}

.card--img {
    width: 100%;
    height: 200px;
    object-fit: cover;
    border-radius: 5px;
}

.card__img--container {
    width: 20%;
    min-width: 150px;
}

.card--info {
    width: 70%;
    align-self: center;
}

.location--info {
    display: flex;
    gap: 1em;
}

.card--location {
    text-transform: uppercase;
}

.card--location i {
    color: #F55A5A;
}

.location--info a {
    color: grey;
    font-size: .8rem;
}

h2 {
    margin: .3em 0;
}

.card--date {
    font-weight: bold;
    margin-bottom: .3em;
    font-size: .8rem;
}

.card--description {
    line-height: 1.4;
}




